<template>
<el-container v-loading.fullscreen.lock="isLoading">
  <el-header class="header" style="background:#EDF2FC;margin-bottom:5px;">  
    <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item>部门管理</el-breadcrumb-item>
    </el-breadcrumb>
  </el-header>
  <el-main class="main"> 
    <el-row>
      <el-input placeholder="输入关键字进行过滤" size="mini" v-model="filterText" class="filterText"></el-input>
      <el-button type="primary" size="mini" icon="el-icon-circle-plus-outline" @click="handleAdd()">添加</el-button>  
    </el-row>
    <el-row>
      <el-tree
        :data="treeData" 
        :props="treeDefaultProps" 
        :empty-text="emptyText"
        node-key="id" 
        ref="tree"
        :filter-node-method="filterNode"
        :render-content="renderContent"
        :default-expand-all="true">
      </el-tree>
    </el-row>

    <el-dialog :visible.sync="mainFormDialogVisible" @submit.native.prevent :close-on-click-modal="false" width="600px">
      <span slot="title">
        {{ editActive ? '编辑' : '添加'}}
      </span>
      <el-form ref="mainForm" :model="mainForm" :rules="mainFormRules" label-position="right" label-width="100px" size="small">
        <el-tabs v-model="activeTabName" type="card">
          <el-tab-pane label="基本信息" name="first">
            <el-form-item label="所属分组">
              <el-cascader :options="editParentTreeData" :props="editParentTreeDefaultProps" clearable change-on-select v-model="mainForm.parentIDPath"></el-cascader>
            </el-form-item>
            <el-form-item label="用户组名称" prop="name">
              <el-input v-model.trim="mainForm.name" auto-complete="off" placeholder="请输入用户组名称" ref="name"></el-input>
            </el-form-item>
            <el-form-item label="是否包含用户">
              <el-switch v-model="mainForm.isIncludeUser"></el-switch>
            </el-form-item>
            <el-form-item label="是否停用">
              <el-switch v-model="mainForm.isDisabled"></el-switch>
            </el-form-item>            
          </el-tab-pane>
          <el-tab-pane label="限制角色" name="second">
            <el-form-item label="限制角色">
              <el-checkbox-group v-model="mainForm.limitRoleIDs">
                <el-checkbox v-for="role in editRoleListData" :label="role.roleID" :key="role.roleID">{{ role.name }}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleMainFormSure(false)">取 消</el-button>
        <el-button type="primary" @click="handleMainFormSure(true)">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="提示" :visible.sync="removeConfirmDialogVisible"  width="320px" center>
      <span>删除该用户组后，相关的数据也将被删除。<br/>确定要删除吗？</span>
      <div slot="footer" class="dialog-footer">
       <el-button @click="handleRemoveSure(false)">取 消</el-button>
       <el-button type="primary" @click="handleRemoveSure(true)">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="leaveTitle" :visible.sync="isstationLeaveAuditFlowForm" width="800px" center>
      <el-form ref="leavemain" :model="leavemain" :rules="stationLeaveFormRules" label-position="right" label-width="100px" size="small">
        <el-form-item label="发起申请" prop='auditRoleID'>
          <el-select v-model="leavemain.domains[0].auditRoleID"  placeholder="请选择" :clearable = 'true'>
          <el-option
          v-for="item in leaveActive.limitRoles"
          :key="item.roleID"
          :label="item.name"
          :value="item.roleID"
          >
        </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="申请流程">
          <el-form-item 
          v-for="(item,index) in leavemain.domains"
          :key="item.index">
            <!-- 申请流程天数 -->
            <el-form-item style="width:100px;float:left;">
              <el-input v-model="item.auditStaffLevelMaxDays" placeholder="天数" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item style="width:150px;float:left;" label="天内(含), 由" label-width="100px">
                <el-cascader :options="dataList" :props="defaultProps"  @change= 'handChangeTree(null,null,index)' clearable filterable placeholder ="部门" :disabled = 'index === 0' change-on-select v-model="item.auditGroupIDPath" style="width:150px;"></el-cascader>
              </el-form-item>
              <el-form-item style="width:150px;float:left;margin-left:110px;">
               <el-select v-model="item.auditRoleID"  :placeholder="(index === 0 && stationLeaveAuditFlowForm.requestRoleID) ? leaveName : '职位'" :disabled = 'index === 0' :clearable = 'true' style="width:150px;">
              <el-option
                v-for="item in limitRoleForm[index]"
                :key="item.RoleID"
                :label="item.Name"
                :value="item.RoleID">
              </el-option>
              </el-select>
              </el-form-item>
              <el-form-item style="width:20px;float:left;" label="审批" label-width="50px">
              </el-form-item>
              <el-button v-if = "index > 0" style="font-size: 15px;position:absolute;top:0;right:20px" type="text" icon="el-icon-remove-outline" @click.prevent="removeLeave(item)">删除</el-button>
            <!-- 申请流程天数结束 -->
          </el-form-item>
          <el-button style="font-size: 15px;position:absolute;top:0;right:20px" type="text" icon="el-icon-circle-plus-outline" @click="addLeave">添加</el-button>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
       <el-button @click="handleLeaveSure(false)">取 消</el-button>
       <el-button type="primary" @click="handleLeaveSure(true)">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog :visible.sync="moveFormDialogVisible" @submit.native.prevent :close-on-click-modal="false" width="600px">
      <span slot="title">
        移动节点: {{ moveActive ? moveActive.name : null }}
      </span>
      <el-form ref="moveForm" :model="moveForm" :rules="moveFormRules" label-position="right" label-width="100px" size="small">
        <el-form-item label="目标节点" prop="targetIDPath">
          <el-cascader :options="editParentTreeData" :props="editParentTreeDefaultProps" clearable change-on-select v-model="moveForm.targetIDPath"></el-cascader>
        </el-form-item>
        <el-form-item label="位置">
          <template>
            <el-radio v-model="moveForm.movingLocation" :label="0" border>目标之下</el-radio>
            <el-radio v-model="moveForm.movingLocation" :label="1" border>目标之上</el-radio>
          </template>
        </el-form-item>
        <el-form-item label="作为子节点" v-if="moveForm.movingLocation === 0">
          <el-switch v-model="moveForm.isChild"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleMoveFormSure(false)">取 消</el-button>
        <el-button type="primary" @click="handleMoveFormSure(true)">确 定</el-button>
      </div>
    </el-dialog>

  </el-main>
</el-container>
</template>

<script>
import api from '@/utils/api'
import _ from 'lodash'

export default {
  data () {
    return {
      // 主要数据
      isLoading: false,
      activeTabName: 'first',
      treeData: null,
      treeDefaultProps: {
        children: 'children',
        value: 'id',
        label: 'name'
      },
      filterText: null,

      // 删除
      removeActive: null,                 // 暂存删除项
      removeConfirmDialogVisible: false,  // 删除确认对话框是否可见
      // 移动
      moveActive: null,                   // 暂存移动项
      moveFormDialogVisible: false,       // 移动对话框是否可见

      // 添加/编辑
      editActive: null,                   // 暂存编辑项，也可用来判断是否添加还是编辑
      mainFormDialogVisible: false,       // 添加/编辑对话框是否可见
      mainForm: {
        groupID: null,                    // String
        name: null,                       // String
        limitRoleIDs: [],                 // Array 不能设置为 null
        parentIDPath: [],                 // Array 不能设置为 null 如果 parentIDPath 无值则作为顶级节点,否则作为子节点。给 cascader 组件使用。
        parentID: null,                   // String
        isIncludeUser: null,              // Bool 是否包含用户
        isDisabled: null                  // Bool 是否停用
      },
      moveForm: {
        sourceID: null,
        targetIDPath: null,               // Array 必须选择。给 cascader 组件使用。
        targetID: null,
        isChild: false,
        movingLocation: 0                 // 0: Under, 1: Above 。 作为子节点，总是 Under；作为兄弟节点则可使用两值任一。
      },
      mainFormRules: {
        name: [
          { required: true, message: '请输入用户组名称', trigger: 'blur' },
          { max: 50, message: '最多支持50个字符', trigger: 'blur' }
        ]
      },
      moveFormRules: {
        targetIDPath: [
          { required: true, type: 'array', message: '请选择目标节点', trigger: 'change' }
        ]
      },
      stationLeaveFormRules: {
        auditRoleID: [
          {required: true, message: '发起申请内容不能为空'}
        ]
      },
      editRoleListData: null,             // 用于编辑对话框内显示的角色列表
      editParentTreeData: null,           // 用于编辑对话框内显示的父节点
      editParentTreeDefaultProps: {
        children: 'children',
        value: 'id',
        label: 'name'
      },
      isstationLeaveAuditFlowForm: false,  // 用于设置请假流程显示
      stationLeaveAuditFlowForm: {
        staffLeaveAuditFlowID: null,
        requestRoleID: null,
        auditStaffLevelMaxDays1: null,
        auditGroupID1: null,
        auditGroupID1Path: [],
        auditRoleID1: null,

        auditStaffLevelMaxDays2: null,
        auditGroupID2: null,
        auditGroupID2Path: [],
        auditRoleID2: null,

        auditStaffLevelMaxDays3: null,
        auditGroupID3: null,
        auditGroupID3Path: [],
        auditRoleID3: null,

        auditStaffLevelMaxDays4: null,
        auditGroupID4: null,
        auditGroupID4Path: [],
        auditRoleID4: null
      },
      leaveName: '',
      leaveTitle: '',
      auditGroupName: '',
      leaveActive: [],
      leaveRole: [],
      limitRoles: [],
      limitRoleForm: [],
      tree: [],
      sourceList: [],
      dataList: [],
      valueObject: '',
      defaultProps: {
        children: 'Children',
        label: 'Name',
        value: 'GroupID'
      },
      leavemain: {
        domains: [
          {
            auditStaffLevelMaxDays: '',
            auditGroupID: '',
            auditGroupIDPath: [],
            auditRoleID: ''
          }
        ]
      }
    }
  },
  mounted () {
    this.getTree()
    this.getRoleBases()
    this.getGroupInfoBaseList()
  },
  computed: {
    emptyText: function () {
      return this.isLoading ? '加载中...' : '暂无数据'
    }
  },
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val)
    }
  },
  methods: {
    getTree () {
      this.isLoading = true
      api.getGroupTree().then(response => {
        this.isLoading = false
        this.treeData = response.data.tree
      }, error => {
        this.isLoading = false
        this.showErrorMessage(error.message)
      })
    },
    filterNode (value, data) {
      if (!value) {
        return true
      }
      return data.name.indexOf(value) !== -1
    },
    getRoleBases () {
      api.getRoleBases().then(response => {
        this.editRoleListData = response.data.list
      }, error => {
        this.showErrorMessage(error.message)
      })
    },
    handleAdd (node, data) {
      console.log('handleAdd', node, data)
      if (!this.validateBaseData()) {
        return
      }
      let parentIDPath = []
      if (data) {
        parentIDPath = data.parentIDPath ? data.parentIDPath : []
        parentIDPath.push(data.id)
      }
      this.activeTabName = 'first'
      this.editActive = null
      this.generateParentTree(null)
      this.mainFormDialogVisible = true
      this.mainForm.groupID = null
      this.mainForm.name = null
      this.mainForm.parentIDPath = parentIDPath
      this.mainForm.parentID = data ? data.id : null
      this.mainForm.limitRoleIDs = []       // 不能设置为 null
      this.mainForm.isIncludeUser = true    // 默认 true
      this.mainForm.isDisabled = false      // 默认 false
      this.$nextTick(() => {
        this.clearValidate('mainForm')
      })
    },
    handleEdit (node, data) {
      console.log('handleEdit', node, data)
      if (!this.validateBaseData() || !data) {
        return
      }
      this.activeTabName = 'first'
      this.editActive = data
      this.generateParentTree(data)
      this.mainFormDialogVisible = true
      this.mainForm.groupID = data.id
      this.mainForm.name = data.name
      this.mainForm.parentIDPath = data.parentIDPath
      this.mainForm.parentID = data.id
      this.mainForm.limitRoleIDs = data.limitRoles.map(m => m.roleID)
      this.mainForm.isIncludeUser = data.isIncludeUser
      this.mainForm.isDisabled = data.isDisabled
      this.$nextTick(() => {
        this.clearValidate('mainForm')
      })
    },
    handleMainFormSure (sure) {
      console.log('handleMainFormSure', sure)
      if (sure) {
        // 提交数据
        if (this.editActive) {
          this.edit()
        } else {
          this.add()
        }
      } else {
        this.mainFormDialogVisible = false
        // this.editActive = null // 注：添加状态 endActive 本就为 null
      }
    },
    handleRemove (node, data) {
      this.removeActive = data
      this.removeConfirmDialogVisible = true
    },
    handleRemoveSure (sure) {
      this.removeConfirmDialogVisible = false
      if (sure) {
        this.remove()
      } else {
        this.removeActive = null
      }
    },
    handleLeave (node, data) {
      this.leaveActive = data
      this.isstationLeaveAuditFlowForm = true
      this.leaveTitle = data.name + ' 请假流程设置'
      // this.auditGroupName = data.name
      // this.limitRoles = data.limitRoles
      // console.log('请假部门数据' + JSON.stringify(this.leavemain))
      this.limitRoleForm = []
      this.leavemain.domains = [
        {
          auditStaffLevelMaxDays: '',
          auditGroupID: '',
          auditGroupIDPath: [],
          auditRoleID: ''
        }
      ]
      // console.log('limitRoleForm   ' + JSON.stringify(this.limitRoleForm[1]))
      this.stationLeaveAuditFlowForm.staffLeaveAuditFlowID = data.id
      if (data.staffLeaveAuditFlow === undefined) {
        this.leavemain.domains[0].auditGroupIDPath = [data.id]
        this.getGroupIDPath(this.treeData, data.id, 0)
        this.handChangeTree(this.leavemain.domains[0].auditGroupIDPath, null, null)
      } else {
        const leaveAuditFlowForm = data.staffLeaveAuditFlow
        const newLeaveForm = {}
        var count = 0
        for (var i in leaveAuditFlowForm) {
          if (leaveAuditFlowForm[i] !== null) {
            if (leaveAuditFlowForm[i].RoleID !== null || leaveAuditFlowForm[i].GroupID !== null) {
              if (i === 'RequestRoleID') {
                this.stationLeaveAuditFlowForm.requestRoleID = leaveAuditFlowForm[i].RequestRoleID
              }
              if (i !== 'RequestRoleID' && i !== 'StaffLeaveAuditFlowID') {
                count++
                newLeaveForm[i] = leaveAuditFlowForm[i]
              }
            }
          }
          // console.log('循环i ' + i + JSON.stringify(leaveAuditFlowForm[i]))
        }
        for (let j = 0; j < count / 3; j++) {
          // console.log('数据22222')
          const leaveLength = (count / 3) - 1
          for (let z = 0; z < leaveLength; z++) {
            const leavemainLength = this.leavemain.domains.length
            if (leavemainLength === (z + 1)) {
              this.leavemain.domains.push({
                auditStaffLevelMaxDays: '',
                auditGroupID: '',
                auditRoleID: '',
                auditGroupIDPath: [],
                key: Date.now()
              })
            }
          }
          // console.log('数据222223')
          for (let x in newLeaveForm) {
            const auditGroupID = 'AuditGroupID' + (j + 1)
            const auditRoleID = 'AuditRoleID' + (j + 1)
            const auditStaffLevelMaxDays = 'AuditStaffLevelMaxDays' + (j + 1)
            // console.log('数据11111', auditGroupID)
            if (x === auditGroupID) {
              this.leavemain.domains[j].auditGroupID = newLeaveForm[x].GroupID
              // const auditGroupIDForm = [newLeaveForm[x].GroupID]
              // this.leavemain[j].auditGroupIDPath = auditGroupIDForm
              // const number = j
              this.getGroupIDPath(this.treeData, newLeaveForm[x].GroupID, j)
              this.handChangeTree(this.leavemain.domains[j].auditGroupIDPath, j, null)
            }
            if (x === auditRoleID) {
              this.leavemain.domains[j].auditRoleID = newLeaveForm[x].RoleID
            }
            if (x === auditStaffLevelMaxDays) {
              this.leavemain.domains[j].auditStaffLevelMaxDays = newLeaveForm[x]
            }
          }
        }
      }
      console.log('请假部门数据' + JSON.stringify(this.leavemain.domains))
    },
    getGroupIDPath (tree, id, j) {
      if (!tree) return
      // console.log('我是大数据' + JSON.stringify(this.treeData))
      for (let node of tree) {
        if (node.id === id) {
          // console.log('我是大数据' + j + JSON.stringify(node.parent))
          this.leavemain.domains[j].auditGroupIDPath = node.parentIDPath ? node.parentIDPath.concat() : []
          this.leavemain.domains[j].auditGroupIDPath.push(id)
          break
        } else {
          this.getGroupIDPath(node.children, id, j)
        }
      }
    },
    handleLeaveSure (stauts) {
      if (!stauts) {
        this.isstationLeaveAuditFlowForm = false
      } else {
        var leavemainList = this.leavemain.domains
        const leavemainLength = leavemainList.length
        this.stationLeaveAuditFlowForm.requestRoleID = leavemainList[0].auditRoleID
        for (let i = 0; i < leavemainLength; i++) {
          const auditIdx = leavemainList[i].auditGroupIDPath.length
          this.stationLeaveAuditFlowForm['auditGroupID' + (i + 1)] = leavemainList[i].auditGroupIDPath[auditIdx - 1]
          this.stationLeaveAuditFlowForm['auditRoleID' + (i + 1)] = leavemainList[i].auditRoleID
          // this.stationLeaveAuditFlowForm['auditGroupID' + (i + 1) + 'Path'] = leavemainList[i].auditGroupIDPath
          this.stationLeaveAuditFlowForm['auditStaffLevelMaxDays' + (i + 1)] = leavemainList[i].auditStaffLevelMaxDays
        }
        const leaveAuditFlowForm = this.stationLeaveAuditFlowForm
        if (leaveAuditFlowForm.auditStaffLevelMaxDays1 === '') {
          this.$message({
            message: '天数不能为空',
            type: 'error'
          })
          return
        }
        if (leaveAuditFlowForm.requestRoleID === '') {
          this.$message({
            message: '发起申请选项不能为空',
            type: 'error'
          })
          return
        }
        this.isLoading = true
        api.getConfigStaffLeaveAuditFlow(leaveAuditFlowForm).then(response => {
          this.isstationLeaveAuditFlowForm = false
          this.isLoading = false
          this.getTree()
        }, error => {
          this.showErrorMessage(error.message)
          this.isLoading = false
        })
        // console.log('提交数据' + JSON.stringify(this.leavemain))
        // console.log('提交数据1' + JSON.stringify(leaveAuditFlowForm))
      }
    },
    addLeave () {
      const leaveLength = this.leavemain.domains.length
      if (leaveLength < 4) {
        this.leavemain.domains.push({
          auditStaffLevelMaxDays: '',
          auditGroupID: '',
          auditRoleID: '',
          auditGroupIDPath: [],
          key: Date.now()
        })
      }
    },
    removeLeave (item) {
      var index = this.leavemain.domains.indexOf(item)
      if (index !== -1) {
        this.leavemain.domains.splice(index, 1)
      }
      this.resetLeaveAuditFlow()
      this.stationLeaveAuditFlowForm.staffLeaveAuditFlowID = this.leaveActive.id
    },
    // 清空数据
    resetLeaveAuditFlow () {
      this.stationLeaveAuditFlowForm = {
        auditStaffLevelMaxDays1: null,
        auditGroupID1: null,
        auditGroupID1Path: [],
        auditRoleID1: null,

        auditStaffLevelMaxDays2: null,
        auditGroupID2: null,
        auditGroupID2Path: [],
        auditRoleID2: null,

        auditStaffLevelMaxDays3: null,
        auditGroupID3: null,
        auditGroupID3Path: [],
        auditRoleID3: null,

        auditStaffLevelMaxDays4: null,
        auditGroupID4: null,
        auditGroupID4Path: [],
        auditRoleID4: null
      }
    },
    handChangeTree (val, _idx, index) {
      if (val === null) {
        val = this.leavemain.domains[index].auditGroupIDPath
      }
      const _index = val.length - 1
      const groupID = val[_index]
      // this.searchCriteriaForm.groupID = groupID
      // const treeData = this.treeData
      // console.log('数据', JSON.stringify(_idx))
      // console.log('字数', JSON.stringify(index))
      const sourceList = this.sourceList
      for (let i = 0; i < sourceList.length; i++) {
        if (sourceList[i].GroupID === groupID) {
          if (_idx === null) {
            if (index !== null) {
              const limitRoleFormLength = this.limitRoleForm.length
              if (index > limitRoleFormLength) {
                this.limitRoleForm.push(sourceList[i].Roles)
              } else {
                this.limitRoleForm[index] = sourceList[i].Roles
              }
            } else {
              this.limitRoleForm.push(sourceList[i].Roles)
            }
          } else {
            this.limitRoleForm.push(sourceList[i].Roles)
          }
          console.log('数据s' + JSON.stringify(sourceList[i].Roles))
        }
      }
      // console.log(groupID)
      // console.log('树' + JSON.stringify(this.treeData))
      // console.log('角色' + JSON.stringify(this.editRoleListData))
    },
    // 获取部门数据
    getGroupInfoBaseList () {
      api.GetGroupInfoBaseList().then(response => {
        this.sourceList = response.data.list
        this.generateTree()
        this.dataList = this.tree
        // console.log('success' + JSON.stringify(this.tree))
      }, error => {
        console.log('errors' + JSON.stringify(error))
      })
    },
    // 添加到总部下
    generateTree () {
      const pid = this.sourceList[0].GroupID
      for (let i = 1; i < this.sourceList.length; i++) {
        if (this.sourceList[i].ParentID === pid) {
          const item = {
            GroupID: this.sourceList[i].GroupID,
            Name: this.sourceList[i].Name,
            // Children: [],
            Roles: this.sourceList[i].Roles,
            Level: this.sourceList[i].Level
          }
          this.tree.push(item)
          this.addChildren(item, i)
        }
      }
    },
    // 递归排列职位
    addChildren (item, currentIndex) {
      for (let i = currentIndex + 1; i < this.sourceList.length; i++) {
        if (this.sourceList[i].ParentID === item.GroupID) {
          const child = {
            GroupID: this.sourceList[i].GroupID,
            Name: this.sourceList[i].Name,
            // Children: [],
            Roles: this.sourceList[i].Roles,
            Level: this.sourceList[i].Level
          }
          if (!item.Children) {
            item.Children = []
          }
          item.Children.push(child)
          this.addChildren(child, i)
        }
      }
    },
    handleMove (node, data) {
      console.log('handleMove', node, data)
      if (!this.validateBaseData() || !data) {
        return
      }
      this.moveActive = data
      this.generateParentTree(data)
      this.moveFormDialogVisible = true
      this.moveForm.sourceID = data.id
      this.moveForm.targetIDPath = []
      this.moveForm.movingLocation = 0
      this.moveForm.isChild = false
      this.$nextTick(() => {
        this.clearValidate('moveForm')
      })
    },
    handleMoveFormSure (sure) {
      console.log('handleMoveFormSure', sure)
      if (sure) {
        this.move()
      } else {
        this.moveFormDialogVisible = false
        this.moveActive = null
      }
    },
    add () {
      this.$refs.mainForm.validate(valid => {
        if (!valid) return false // 客户端校验未通过
        this.isLoading = true
        this.mainForm.parentID = this.mainForm.parentIDPath && this.mainForm.parentIDPath.length
          ? this.mainForm.parentIDPath[this.mainForm.parentIDPath.length - 1]
          : null
        const params = this.mainForm
        api.addGroup(params).then(response => {
          this.isLoading = false
          this.mainFormDialogVisible = false
          this.getTree()
        }, error => {
          this.isLoading = false
          this.showErrorMessage(error.message)
        })
      })
    },
    edit () {
      if (!this.editActive) {
        this.showErrorMessage('异常：无编辑目标')
        return
      }
      this.$refs.mainForm.validate(valid => {
        if (!valid) return false // 客户端校验未通过
        this.isLoading = true
        this.mainForm.parentID = this.mainForm.parentIDPath && this.mainForm.parentIDPath.length
          ? this.mainForm.parentIDPath[this.mainForm.parentIDPath.length - 1]
          : null
        const params = this.mainForm
        api.editGroup(params).then(response => {
          this.isLoading = false
          this.editActive = null
          this.mainFormDialogVisible = false
          this.getTree()
        }, error => {
          this.isLoading = false
          this.showErrorMessage(error.message)
        })
      })
    },
    remove () {
      if (!this.removeActive) return
      const params = {
        groupID: this.removeActive.id
      }
      this.isLoading = true
      api.removeGroup(params).then(response => {
        this.isLoading = false
        this.removeActive = null
        this.getTree()
      }, error => {
        this.isLoading = false
        this.showErrorMessage(error.message)
      })
    },
    move () {
      if (!this.moveActive) return
      this.$refs.moveForm.validate(valid => {
        if (!valid) return false // 客户端校验未通过
        const params = {
          sourceID: this.moveActive.id,
          targetID: this.moveForm.targetIDPath[this.moveForm.targetIDPath.length - 1],
          isChild: this.moveForm.movingLocation === 0 ? this.moveForm.isChild : null,
          movingLocation: this.moveForm.movingLocation
        }
        console.log(this.moveForm)
        this.isLoading = true
        api.moveGroup(params).then(response => {
          this.isLoading = false
          this.moveFormDialogVisible = false
          this.moveActive = null
          this.getTree()
        }, error => {
          this.isLoading = false
          this.showErrorMessage(error.message)
        })
      })
    },
    validateBaseData () {
      if (!this.editRoleListData) {
        this.showErrorMessage('基础数据缺失：角色列表')
        return false
      }
      return true
    },
    generateParentTree (data) {
      // 在添加或编辑用户组时，需要将系统用户组
      if (!this.treeData) {
        // this.treeData 实际上一定有值的
        this.editParentTreeData = []
        return
      }
      if (!data) {
        // 添加时，过滤系统用户组
        this.editParentTreeData = this.treeData.filter((element, index, array) => { return !element.isSystem })
      } else {
        // 编辑或移动时，过滤系统用户组和自身树(需递归)
        this.editParentTreeData = _.cloneDeep(this.treeData)
        // 系统用户组只在第一级
        this.editParentTreeData = this.editParentTreeData.filter((element, index, array) => { return !element.isSystem })
        this.filterTree(this.editParentTreeData, data)
      }
    },
    filterTree (tree, data) {
      for (let i = 0; i < tree.length; i++) {
        const item = tree[i]
        // tree 通过 clone 而来，不能直接比较对象
        if (item.id === data.id) {
          // 在第一级就找到
          tree.splice(i, 1)
          break
        } else if (item.children) {
          this.filterTree(item.children, data)
        }
      }
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    clearValidate (formName) {
      this.$refs[formName].clearValidate()
    },
    showErrorMessage (message) {
      this.$message({
        message: message,
        type: 'error'
      })
    },
    renderContent (h, { node, data, store }) {
      return (
        <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 10px;">
          <span>
            <span>{ node.label }</span>
          </span>
          <span>
            <span style="font-size: 12px;"> { data.limitRoles ? data.limitRoles.map(m => m.name).join(' ') : '' } </span>
          </span>
          <span v-show={ !data.isSystem }>
            <span style="font-size: 12px;" v-show={ data.isDisabled }>[停用] </span>
            <span style="font-size: 12px;" v-show={ data.isIncludeUser }>[用户] </span>
            <el-button style="font-size: 12px;" type="text" icon="el-icon-edit-outline" v-show={ data.level === 5 } onClick={ (e) => {
              e.stopPropagation()
              e.preventDefault()
              this.handleLeave(node, data)
            }}>请假流程</el-button>
            <el-button style="font-size: 12px;" type="text" icon="el-icon-edit-outline" onClick={ (e) => {
              e.stopPropagation()
              e.preventDefault()
              this.handleEdit(node, data)
            }}>编辑</el-button>
            <el-button style="font-size: 12px;" type="text" icon="el-icon-circle-plus-outline" onClick={ (e) => {
              e.stopPropagation()
              e.preventDefault()
              this.handleAdd(node, data)
            }}>添加</el-button>
            <el-button style="font-size: 12px;" type="text" icon="el-icon-rank" onClick={ (e) => {
              e.stopPropagation()
              e.preventDefault()
              this.handleMove(node, data)
            }}>移动</el-button>
            <el-button style="font-size: 12px;" type="text" icon="el-icon-remove-outline" onClick={ (e) => {
              e.stopPropagation()
              e.preventDefault()
              this.handleRemove(node, data)
            }}>删除</el-button>
          </span>
        </span>)
    }
  }
}
</script>

<style lang="scss" scoped>

.filterText {
  width: 200px;
  margin-right: 12px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
// 角色
.el-checkbox {
  display: block;
  margin: 0;
}
// 用户组
.el-cascader {
  width: 100%;
}

</style>


